import { mapActions } from 'vuex';
import MapMarker from 'vue-material-design-icons/MapMarker';
import AccountMultiplePlus from 'vue-material-design-icons/AccountMultiplePlus';
import Send from 'vue-material-design-icons/Send';
import { Button } from '@nextcloud/vue';
import moment from '@nextcloud/moment';
import Loader from './Loader.vue';
import CollaboratorsSelectionForm from './CollaboratorsSelectionForm.vue';
export default {
  name: 'AlbumForm',
  components: {
    Button,
    MapMarker,
    AccountMultiplePlus,
    Send,
    Loader,
    CollaboratorsSelectionForm
  },
  props: {
    album: {
      type: Object,
      default: null
    },
    displayBackButton: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      showCollaboratorView: false,
      albumName: '',
      albumLocation: '',
      loading: false
    };
  },

  computed: {
    editMode() {
      return this.album !== null;
    }

  },

  mounted() {
    if (this.editMode) {
      this.albumName = this.album.basename;
      this.albumLocation = this.album.location;
    }

    this.$nextTick(() => {
      this.$refs.nameInput.focus();
    });
  },

  methods: { ...mapActions(['createAlbum', 'renameAlbum']),

    submit() {
      let collaborators = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : [];

      if (this.editMode) {
        this.handleUpdateAlbum(collaborators);
      } else {
        this.handleCreateAlbum(collaborators);
      }
    },

    async handleCreateAlbum() {
      let collaborators = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : [];

      try {
        this.loading = true;
        const album = await this.createAlbum({
          album: {
            basename: this.albumName,
            size: 0,
            lastmod: moment().unix(),
            location: this.albumLocation,
            collaborators
          }
        });
        this.$emit('done', {
          album
        });
      } finally {
        this.loading = false;
      }
    },

    async handleUpdateAlbum() {
      let collaborators = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : [];

      try {
        this.loading = true;
        const album = await this.renameAlbum({
          currentAlbumName: this.album.basename,
          newAlbumName: this.albumName
        });
        this.$emit('done', {
          album
        });
      } finally {
        this.loading = false;
      }
    },

    back() {
      this.$emit('back');
    }

  }
};